import React from "react";
import {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import "./TodaysPay.css";

function TodaysPay() {
  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.info.main,
      color: theme.palette.common.white,
      padding: "0.3rem",
    },
  }))(TableCell);
  return (
    <div className="payment_container">
      <div className="payment_header">Market Stats</div>
      <div className="total_earn">3000 Earned</div>
      <div className="order_status">
        <div className="order_status_head">Order Stats</div>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow style={{ backgroundColor: "#444444", Color: "white" }}>
                <StyledTableCell>Total</StyledTableCell>
                <StyledTableCell>Completed</StyledTableCell>
                <StyledTableCell>Cancelled</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell>20</TableCell>
                <TableCell>18</TableCell>
                <TableCell>2</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      </div>
      <div className="payment_status">
        <div className="order_status_head">Payment Stats</div>
        <div className="payment_stat_card recived">
          <div>
            <span className="payment_stat_head_data ">Recived :</span> 15
          </div>
          <div className="payment_stat_data">2500INR</div>
        </div>
        <div className="payment_stat_card pending">
          <div>
            <span className="payment_stat_head_data ">Pending :</span> 3
          </div>
          <div className="payment_stat_data">500INR</div>
        </div>
      </div>
    </div>
  );
}

export default TodaysPay;
