import React, { useEffect, useState } from "react";
import {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { todaysorder } from "../../Data/todaysorder";
import "../LiveOrders/LiveOrders.css";

function TodaysOrder() {
  const [liveorderdata, setLiveorderdata] = useState(todaysorder);

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
      fontWeight: 500,
    },
  }))(TableCell);
  // useEffect(() => {
  //     const interval = setInterval(() => {
  //         axios.get('http://localhost:3333/liveorders')
  //         .then(res => {
  //             console.log("res",res.data);
  //             setLiveorderdata(res.data);
  //         })
  //       }, 10000);
  //       return () => clearInterval(interval);
  // },[])

  return (
    <div className="liveorder_con">
      <div className="liveoredr_head">Today's Order History</div>
      <div className="todaysorder_table">
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow style={{ backgroundColor: "#444444", Color: "white" }}>
                <StyledTableCell>Order Id</StyledTableCell>
                <StyledTableCell>Customer Name</StyledTableCell>
                <StyledTableCell>Customer ID</StyledTableCell>
                <StyledTableCell>Product ID</StyledTableCell>
                <StyledTableCell>Product</StyledTableCell>
                <StyledTableCell>Category</StyledTableCell>
                <StyledTableCell>Amount</StyledTableCell>
                <StyledTableCell>Order Status</StyledTableCell>
                <StyledTableCell>Paid By</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {liveorderdata.map((orderData, id) => {
                return (
                  <TableRow key={id}>
                    <TableCell>{orderData.Order_id}</TableCell>
                    <TableCell>{orderData.Customer_name}</TableCell>
                    <TableCell>{orderData.Customer_id}</TableCell>
                    <TableCell>{orderData.product_id}</TableCell>
                    <TableCell>{orderData.product}</TableCell>
                    <TableCell>{orderData.category}</TableCell>
                    <TableCell>{orderData.amount}</TableCell>
                    <TableCell>
                      <span
                        className={`${
                          orderData.order_satus === "Sucess"
                            ? "sucess"
                            : "cancel"
                        }`}
                      >
                        {orderData.order_satus}
                      </span>
                    </TableCell>
                    <TableCell>{orderData.paid_by}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </div>
  );
}
export default TodaysOrder;
