import React from "react";
import order from "../../Assets/StateImage/order.svg";
import monthly from "../../Assets/StateImage/monthly.svg";
import cancel from "../../Assets/StateImage/cancel.svg";
import "./StateCard.css";

function StateCard() {
  console.log("cards");
  return (
    <div className="statecard_container">
      <div className="statecard order">
        <div className="statecard_title">Today's Orders</div>
        <img src={order} alt="order" className="statecard_img" />
        <div className="statecard_body">20</div>
      </div>

      <div className="statecard monthly">
        <div className="statecard_title">Monthly Order</div>
        <img src={monthly} alt="order" className="statecard_img" />
        <div className="statecard_body">20</div>
      </div>

      <div className="statecard cancel">
        <div className="statecard_title">Cancel orders</div>
        <img src={cancel} alt="order" className="statecard_img" />
        <div className="statecard_body">20</div>
      </div>
    </div>
  );
}

export default StateCard;
