import React from "react";
import { Pie } from "react-chartjs-2";
import "./TodayChart.css";

function TodayChart() {
  const chartData = {
    labels: ["Sucess", "Cancel", "Pending"],
    datasets: [
      {
        label: "Markets Monitored",
        backgroundColor: [
          "#268C38", //Sucess
          "#901831", //cancel
          "#D6FF00", //pending
        ],
        data: [60, 10, 30],
      },
    ],
  };

  const options = {
    legend: {
      display: true,
      position: "right",
    },
    // maintainAspectRatio: false,
    // responsive: false,
  };
  console.log("chart");
  return (
    <div className="chart_con">
      <div className="chart_head">Today's Order</div>
      <div className="chart_body">
        <Pie data={chartData} options={options} />
      </div>
    </div>
  );
}

export default TodayChart;
