import React from "react";
import Comments from "../Comments/Comments";
import LiveOrders from "../LiveOrders/LiveOrders";
import StateCard from "../StateCard/StateCard";
import TodayChart from "../TodayChart/TodayChart";
import TodaysOrder from "../TodaysOrder/TodaysOrder";
import TodaysPay from "../TodaysPay/TodaysPay";
import WeeklyChart from "../WeeklyChart/WeeklyChart";
import "./Dashboard.css";

function Dashboard({ toggle }) {
  console.log("Dashboard");
  return (
    <div className={`dashboard ${toggle ? "dashboardFull" : "dashboardHalf"}`}>
      <div className="sec1">
        <div className="cards">
          <StateCard />
        </div>
        <div className="live_order box_shadow">
          <LiveOrders />
        </div>
        <div className="chart_today">
          <TodayChart />
        </div>
      </div>
      <div className="sec3">
        <div className="comments">
          <Comments />
        </div>
        <div className="weekly_chart">
          <WeeklyChart />
        </div>
      </div>
      <div className="sec2">
        <div className="todays_order">
          <TodaysOrder />
        </div>
        <div className="todays_pay">
          <TodaysPay />
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
